input[type="file"] {
    display: none;
}
input[type="file"] :hover {
    display: none;
  
}
.custom-file-upload {
    /* border: 1px solid #fcfbfbda; */
    border-radius: 7px;
    font-size: 12px;
    color: rgb(253, 253, 253);
    background-color: red;
    display: inline-block;
    padding: 3px 6px;
    cursor: pointer;
}
.custom-file-upload-xml {
    /* border: 1px solid #fcfbfbda; */
    border-radius: 7px;
    font-size: 12px;
    color: rgb(253, 253, 253);
    background-color: rgb(4, 203, 230);
    display: inline-block;
    padding: 3px 6px;
    cursor: pointer;
}
.custom-file-upload-ZIP{
    
    border-radius: 7px;
    font-size: 12px;
    color: rgb(253, 253, 253);
    background-color: rgb(79, 169, 253) !important;
    display: inline-block;
    padding: 3px 6px;
    cursor: pointer;
}
.color-danger{
    color: red !important;
}
.color-gray{
    color:rgb(138, 137, 137);
}
.color-primary{
    color:rgb(104, 174, 255);
}
.color-success{
    color:rgb(0, 205, 0);
}
.selected{
    background-color: #b0a1e752!important;
}
.table-shipping{
    height: '70vh';
    overflow-y: 'scroll';
}
.ml-3{
    margin-left:3vh;
}

@media (max-width: 1200px) {
    .img-logo{width: 20% !important;}
    .table-shipping{  overflow-x: 'hidden'; }
}
@media (max-width: 992px) {
    .img-logo{width: 15% !important;}
    .table-shipping{overflow-x: 'hidden';}
}
@media (max-width: 768px) {
    .img-logo{width: 80% !important;}
    .table-shipping{overflow-x: 'scroll';}
}
@media (max-width: 576px) {
    .img-logo{width: 20% !important;}
    .table-shipping{overflow-x: 'scroll';}
}

@media (max-width: 1024px) {
    .tittle-navbar{
        font-size: 15px!important;
    }
}
