.table-hover  td :hover{
    cursor: pointer !important;
}
.table-hover:hover{
    cursor: pointer !important;
    /* background-color: rgba(241, 246, 255, 0.89) !important; */
}
.shadow{
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.932);
-webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.954);
-moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.946);
}
.tableFixHead {
    overflow-y: auto; /* make the table scrollable if height is more than 200 px  */
    height: 30px;/*  gives an initial height of 200px to the table */
  }
  .tableFixHead thead th {
    background-color: rgba(84, 98, 247, 0.975);
    position: sticky; /* make the table heads sticky */
    color: #fff;
    top: 0px; /* table head will be placed from the top of the table and sticks to it */
  }
  table {
    border-collapse: collapse; /* make the table borders collapse to each other */
    width: 100%;
    
  }
  /* th,
  td {
    padding: 8px 16px;
    border: 1px solid #ccc;
  } */
  th {
    background: #eee;
  }
  th:nth-child(1) {
    border-radius: 15px 0px 0px 0px;
  }
  th:nth-last-child(1) {
    border-radius: 0px 15px 0px 0px;
  }
  .subtableFixHead thead th {
    background-color: rgb(66 173 56 / 98%);
    position: sticky;
    color: #fff;
    top: 0px;
}